import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
//import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import { ListItemAvatar, Link} from '@material-ui/core';
import {FixedSizeList as List} from 'react-window';

const useStyles = makeStyles(theme => ({
  root: {
    minWidth: '340px',
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
  inline: {
    display: 'inline',
  },
}));

const Row = props => {
    
    const {data, index, style} = props;
    return (
    <ListItem button key={index} style={style} alignItems="flex-start">
    <ListItemAvatar>
        <Typography variant="h5" gutterBottom>P{index}</Typography>
    </ListItemAvatar>
    <ListItemText
      primary={data[index].backupnotes}
      secondary={
        <React.Fragment>
          <Typography
            component="span"
            variant="body2"
            style= {{display: "inline"}}
            color="textPrimary"
          >
          </Typography>
        {`Age: ${data[index].agebracket===""? "Unknown" : data[index].agebracket}`}
        &emsp;
        {`Gender: ${data[index].gender ===""? "Unknown" : data[index].gender}`}
        &emsp;
        {`Detected: ${data[index].detectedcity}, ${data[index].detectedstate} - ${data[index].dateannounced}`}<br/>
        {`Caused: ${data[index].notes}`}<br/>
        {`Status: ${data[index].currentstatus} - ${data[index].statuschangedate}`}<br/>
        {data[index].source1===""? null : (<Link href={data[index].source1} rel="noopener noreferrer" target="_blank">
          <Typography component="span">
            Source
          </Typography>
        </Link>)}
        &emsp;
        {data[index].source2===""? null : (<Link href={data[index].source2} rel="noopener noreferrer" target="_blank">
          <Typography component="span">
            Source
          </Typography>
        </Link>)}
        </React.Fragment>
      }
    />
  </ListItem>
)}


const PatientList = (props) => {
  const classes = useStyles();
  return (
      <List
        className={classes.root}
        height={1000}
        itemCount={props.patientInfo.length}
        itemSize={140}
        width={"100%"}
        itemData={props.patientInfo}
      >
        {Row}
      </List>
  )
}
export default PatientList